}
.asdasd-enter .noty__inner,
.asdasd-leave-to .noty__inner {
  width: 100%;
}
.asdasd-enter .noty__inner:before,
.asdasd-leave-to .noty__inner:before {
  width: 100%;
}
.noty {
  position: relative;
  background-color: #fdbd3e;
  padding: 2px 8px 2px 14px;
  margin-top: 8px;
  color: #1c1721;
  overflow: hidden;
}
.noty__inner {
  position: absolute;
  top: 0;
  left: 0;
  width: 6px;
  height: 100%;
  background-color: #ff801e;
  -webkit-transition: width 250ms ease 25ms;
  transition: width 250ms ease 25ms;
}
.noty__inner:before {
  content: '';
  width: 0;
  height: 100%;
  background-color: #ff544a;
  -webkit-transition: width 150ms ease 100ms;
  transition: width 150ms ease 100ms;
  position: absolute;
  top: 0;
  left: 0;
}
